export default {
	data() {
		return {
			isShow:false,
			tips:{
				"userCode": '',
				"leftPerformance": 0,
				"rightPerformance": 0,
				"dayPerformance": 0
			},
			isShowTip: false,
			userCode: '',
			tree:'',
			active:'',
			last:[],
			orgion:'',
			searchCode:''
		};
	},
	onLoad:function(){
		this.userCode = this.$store.state.userCode
		this.orgion = this.$store.state.userCode
		this.last.push(this.orgion)
		this.fetch()
	},
	methods:{
		backtoLast:function(){
			
			let tmp = this.last.pop()
			if (tmp == this.orgion){
				this.userCode = tmp
				console.log('wfwefwef')
				this.last = []
				this.last.push(this.orgion)
				this.fetch()
				return
			}
			
			this.userCode = tmp
			this.fetch()
		},
		longPressText:function(e){
			this.userCode = e
			this.last.push(e)
			console.log(this.last)
			this.fetch()
		},
		async fetch(){
			let data ={
				userCode: this.userCode
			}
			const res = await this.$http.get('/user/tree',{data: data})
			this.tree = res.data.data
			console.log(res)
		},
		async inputConfirm(){
			this.userCode = this.searchCode
			this.fetch()
		},
		backtoTop:function(){
			this.userCode = this.orgion
			this.fetch()
		},
			
		tapShow:function(e){
			console.log(e)
		},
		async tapText(id,e){
			
			if (this.active ==id){
				this.active =''
				return
			}else {
				this.active = id
			}
			
			let data ={ 
				userCode: id 
			}
			const res = await this.$http.get('/user/performance',{data: data})
		    console.log(res)
			this.tips = res.data.data
		},
		
	}
}
